.dashContainer{
  display:grid;
  grid-template-columns: repeat(6,1fr);
  grid-template-rows: repeat(6,1fr);
  height:100vh;
  text-align: center;
}

.cookie{
  grid-column:2;
  grid-row:6;
  margin-bottom:20px;
}

.cake{
  grid-column:5;
  grid-row:5;
  margin-bottom: 100px;
  margin-right:30px;
}

.candies{
  grid-column:3;
  grid-row:6;
  margin-bottom: 0px;

}

.other{
  grid-column:3;
  grid-row:5;
  margin-right:170px;
  margin-top:100px;
}

.pies{
  grid-column:1;
  grid-row:6;
  margin-top:10px;
}

.pastries{
  grid-column:4;
  grid-row:5;
  margin-left:50px;
}


.brownies{
  grid-column:5;
  grid-row:4/5;
  margin-right:50px;
}

@media screen and (max-height:850px){
  .other{
    margin-top:120px;
  }
  .cake{
    margin-bottom:70px;
  }
  .pies{
    margin-top:20px;
  }
}

@media screen and (max-height:860px){
  .other{
    margin-top:120px;
  }
  .cake{
    margin-bottom:90px;
  }
  .pies{
    margin-top:30px;
  }
}

@media screen and (max-height:760px){
  .dashContainer{
    display:flex;
    flex-direction: column;
  }
}

@media screen and (max-height:800px){
  .cake{
    grid-column:5;
    grid-row:5;
    margin-bottom:50px
  }
}

@media screen and (max-width:760px){
  .dashContainer{
    display:flex;
    flex-direction: column;
  }
}


@media screen and (max-width:736px){
  .dashContainer{
    display:flex;
    flex-direction: column;
  }
}